import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import {
  Button,
  Container,
  Header,
  Icon,
  Menu,
  Responsive,
  Segment,
  Sidebar,
  Visibility,
  Input,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'
import {Carousel} from 'antd'
import 'antd/dist/antd.css'
import LoginForm from '../home/component/logInForm'
import SignupFrom from '../home/component/signUpForm'
import backPic from '../../statics/background.jpg'
import Account from '../../component/account'


// Heads up!
// We using React Static to prerender our docs with server side rendering, this is a quite simple solution.
// For more advanced usage please check Responsive docs under the "Usage" section.
const getWidth = () => {
  const isSSR = typeof window === 'undefined'

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}

/* eslint-disable react/no-multi-comp */
/* Heads up! HomepageHeading uses inline styling, however it's not the best practice. Use CSS or styled components for
 * such things.
 */
const HomepageHeading = ({ mobile }) => (
  <Container text textAlign={mobile? 'center' : 'left'}>
    <Header
      as='h1'
      content='PowExChain'
      inverted
      style={{
        fontSize: mobile ? '2.2em' : '5em',
        fontWeight: 'normal',
        fontFamily: '"Trebuchet MS", Helvetica, sans-serif',
        marginBottom: 0,
        marginTop: mobile ? '3em' : '3.3em',
      }}
    />
    <Header
      as='h2'
      content='Power the Future.'
      inverted
      style={{
        fontSize: mobile ? '1.2em' : '2.5em',
        fontWeight: 'normal',
        fontFamily: '"Trebuchet MS", Helvetica, sans-serif',
        marginTop: mobile ? '0.5em' : '1.5em',
      }}
    />
  </Container>
)

HomepageHeading.propTypes = {
  mobile: PropTypes.bool,
}


class Desktop extends Component {
  state = {}
  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  handleScrollTop=()=>{
    let scrollToTop = window.setInterval(function() {
      let pos = window.pageYOffset;
      if ( pos > 0 ) {
          window.scrollTo( 0, pos - 150 ); // how far to scroll on each step
      } else {
          window.clearInterval( scrollToTop );
      }
    }, 1);
  }


  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
        <Segment
          inverted
          textAlign='center'
          style={this.props.barSelect === 'Home'? { minHeight: 800, padding: '0.5em 0em', backgroundImage: 'url(' + backPic + ')', backgroundSize: 'cover', backgroundRepeat: 'no-repeat' }:
                {height: 70, padding: '0.5em 0em', backgroundColor: 'black'}}
          vertical
        >
          <Menu
            fixed={fixed ? 'top' : null}
            inverted={!fixed}
            pointing={!fixed}
            secondary={!fixed}
            size='large'
          >
            <Container>
              <Link to='/' onClick={this.props.handleSelect}><Menu.Item name='Home' active={this.props.barSelect === 'Home'} /></Link>
              <Link to='/exchange' onClick={this.props.handleSelect}><Menu.Item name='Exchange' active={this.props.barSelect === 'Exchange'}/></Link>
              <Link to='/asset' onClick={this.props.handleSelect}><Menu.Item name='Asset' active={this.props.barSelect === 'Asset'} /></Link>

              <Menu.Item position='right' style={{marginBottom:'5px'}} >
                <Input style={{ width: 300 + 'px'}} icon='search' placeholder='Search by Address / Txhash / Block ...'/>
              </Menu.Item>
              {!this.props.loginStatus ? 
                <Menu.Item position='right'>
                  <Link to='/login'><Button inverted={!fixed}> Log in </Button></Link>
                  <Link to='/signup'><Button inverted={!fixed}> Sign up </Button></Link>
                </Menu.Item> : <Menu.Item position='right'>
                  <Account befixed={fixed} />
                  <Button inverted={!fixed} primary={fixed} style={{ marginLeft: '0.5em' }} onClick={this.props.logout}>Log Out </Button>
                </Menu.Item>}
            </Container>
          </Menu>
          {this.props.barSelect === 'Home'?<HomepageHeading />:null}
        </Segment>
        </Visibility>
        {children}
        {this.props.barSelect==='Home'? <Button  icon='angle up' size='huge' circular onClick={this.handleScrollTop} style={{position: 'fixed',right: '3em',bottom: '4em',display: fixed?'block':'none'}} />:null}
        
      </Responsive>
    );
  }
}

Desktop.propTypes = {
  children: PropTypes.node,
}

class Mobile extends Component {
  state = {}

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state
    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >
          <Link to='/' onClick={this.props.handleSelect}><Menu.Item name='Home' active={this.props.barSelect === 'Home'} /></Link>
          <Link to='/exchange' onClick={this.props.handleSelect}><Menu.Item name='Exchange' active={this.props.barSelect === 'Exchange'}/></Link>
          <Link to='/asset' onClick={this.props.handleSelect}><Menu.Item name='Asset' active={this.props.barSelect === 'Asset'} /></Link>
          <Menu.Item >
          <Input icon='search' placeholder='Search by Address / Txhash / Block ...' />
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            inverted
            textAlign='center'
            style={this.props.barSelect === 'Home'?{ minHeight: 400, padding: '1em 0em',backgroundImage: 'url('+backPic+')',backgroundSize: 'cover',backgroundRepeat: 'no-repeat' }:{height: 70, padding: '1em 0em', backgroundColor: 'black'}}
            vertical
          >
            <Container>
              <Menu inverted pointing secondary size='large'>
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name='sidebar' />
                </Menu.Item>
                {!this.props.loginStatus ?
                  <Menu.Item position='right'>
                  <Link to='/login'><Button inverted={true}> Log in </Button></Link>
                  <Link to='/signup'><Button inverted={true}> Sign up </Button></Link>
                  </Menu.Item> : <Menu.Item position='right'>
                    <Account />
                    <Button inverted style={{ marginLeft: '0.5em' }} onClick={this.props.logout}>Log Out </Button>
                  </Menu.Item>}
              </Menu>
            </Container>
            {this.props.barSelect === 'Home'?<HomepageHeading mobile />:null}
          </Segment>
          {children}
        </Sidebar.Pusher>
      </Responsive>
    );
  }
}

Mobile.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}


const mapStateToProps = (state) => {
  return {
    loginStatus: state.logIn.login,
    barSelect: state.selectPage.bar,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    logout(){
      dispatch({
        type: 'logout'
      })
    },

    handleSelect(e){
      dispatch({
        type: 'changePage',
        value: e.target.innerHTML
      })
    },
  }
}


const DesktopContainer = connect(mapStateToProps,mapDispatchToProps)(Desktop);
const MobileContainer = connect(mapStateToProps,mapDispatchToProps)(Mobile);

export default ResponsiveContainer