import React, { Component } from 'react'
import {connect} from 'react-redux'
import {
  Form, Input, Button,Row,Col,Select, Alert
} from 'antd'
import 'antd/dist/antd.css'
import axios from 'axios'


class Buy extends Component {
  constructor() {
    super();
    };
  
  componentDidMount=()=>{
  }

  state={
    buyPrice:null,
    buyAmount:null,
    buyPower:null,
    buyType:null,
    sellPrice:null,
    sellAmount:null,
    sellPower:null,
    sellType:null,
  }

  render(){
    const {buyPrice,buyAmount,buyPower,buyType,sellPrice,sellAmount,sellPower,sellType}=this.state
    return (
      <div>
      <Row>
        <Col span={12}>
        <Form layout='horizontal'>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{span:21}}
          >
            <Input onChange={(e)=>{this.setState({buyPrice:e.target.value})}} value={buyPrice} placeholder="UNIT-PRICE" />
          </Form.Item>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{span:21}}
          >
            <Input onChange={(e)=>{this.setState({buyAmount:e.target.value})}} value={buyAmount} placeholder="AMOUNT(kWh)" />
          </Form.Item>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{span:21}}
          >
            <Input onChange={(e)=>{this.setState({buyPower:e.target.value})}} value={buyPower} placeholder="POWER(W)" />
          </Form.Item>
          <Form.Item
            style={{margin:'0', padding:'0'}}
            wrapperCol={{span:21}}
          >
            <Select placeholder='POWER TYPE' onChange={(e)=>{this.setState({buyType:e})}} >
              <Select.Option value='wind'>Wind</Select.Option>
              <Select.Option value='solar'>Solar</Select.Option>
              <Select.Option value='water'>Water</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item style={{margin:'0 0', padding:'0'}}>
            <h4>
              Total: {(buyAmount*buyPrice).toFixed(2)}
            </h4>
          </Form.Item >
          <Form.Item style={{margin:'0 0', padding:'0'}}>
            <Button style={{width:'87.5%',backgroundColor:'#FA5252',border:'none'}} type="primary" onClick={()=>{console.log(buyAmount,buyAmount*buyPrice,buyType)}} >Buy</Button>
          </Form.Item>
        </Form>
        </Col>
        <Col span={12}>
        <Form layout='horizontal'>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{offset:3, span:21}}
          >
            <Input  placeholder="UNIT-PRICE" onChange={(e)=>{this.setState({sellPrice:e.target.value})}} value={sellPrice} />
          </Form.Item>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{offset:3 ,span:21}}
          >
            <Input onChange={(e)=>{this.setState({sellAmount:e.target.value})}} value={sellAmount} placeholder="AMOUNT(kWh)" />
          </Form.Item>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{offset:3 ,span:21}}
          >
            <Input onChange={(e)=>{this.setState({sellPower:e.target.value})}} value={sellPower} placeholder="POWER(W)" />
          </Form.Item>
          <Form.Item
            style={{margin:'0 0', padding:'0'}}
            wrapperCol={{offset:3, span:21}}
          >
            <Select placeholder='POWER TYPE' onChange={(e)=>{this.setState({sellType:e})}} >
              <Select.Option value='wind'>Wind</Select.Option>
              <Select.Option value='solar'>Solar</Select.Option>
              <Select.Option value='water'>Water</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item wrapperCol={{offset:3}} style={{margin:'0 0', padding:'0'}}>
            <h4>
              Total: {(sellAmount*sellPrice).toFixed(2)}
            </h4>
          </Form.Item>
          <Form.Item style={{margin:'0 0', padding:'0'}} wrapperCol={{offset:3}}>
            <Button onClick={()=>{axios.post('http://211.71.8.213:8080/tx/delivery',
              {
                "sessionId": this.props.sessionId,
                "timestampSell": this.props.current,
                "tx": [
                  {
                    "timestampExpire": this.props.expire,
                    "power": sellPower,
                    "value": (sellAmount * sellPrice).toFixed(2),
                    "amount": sellAmount,
                    "type": sellType,
                    "inputData": ""
                  }
                ]
              }).then(() => {
                this.setState({
                  buyPrice: null,
                  buyAmount: null,
                  buyPower: null,
                  buyType: null,
                  sellPrice: null,
                  sellAmount: null,
                  sellPower: null,
                  sellType: null,
                }); alert('Transaction request submitted successfully') ;this.props.refreshPool()
              })
                }} style={{width:'100%',backgroundColor:'#12B886',border:'none'}} type="primary">Sell</Button>
          </Form.Item>
        </Form>
        </Col>
      </Row>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    sessionId: state.logIn.sessionId
  }
}

export default connect(mapStateToProps,null)(Buy);
