class Market extends Component{
    render(){
      return(
        <List
        style={{ height:'375px' }}
        header={<div style={{fontSize:'1em',fontWeight:'bold'}}>Market Trades</div>}
        dataSource={data}
        renderItem={(item,index) => (index===0?<Row style={{marginTop:'0.3em'}}><Col span={8}><div style={{fontSize:'1em',color:'#7F8FA4'}}>{item.price}</div></Col><Col span={8}><div style={{fontSize:'1em',color:'#7F8FA4'}}>{item.amount}</div></Col><Col span={8}><div style={{fontSize:'1em',color:'#7F8FA4'}}>{item.time}</div></Col></Row>:
        <Row style={{marginTop:'0.2em'}}><Col span={8}><div style={{fontWeight:'500'}}>{item.price}</div></Col><Col span={8}><div style={{fontWeight:'500'}}>{item.amount}</div></Col><Col span={8}><div style={{fontWeight:'500'}}>{item.time}</div></Col></Row>)}
        />
      )
    }
}

export default Market