import axios from 'axios'
import React from 'react'
import {Redirect} from 'react-router-dom'

export const login = (account, password) => {
  return (dispatch) => {
    axios.post('http://211.71.8.213:8080/usr/login',{"account":account,"password":password}).then((res) => {
      console.log(res.data)
      if(res.data.status===824){
      alert('Password Wrong!')
      }
      else if (res.data.status===823){
      alert('Account name does not exist.')
      }
      else if (res.data.status===722){
        dispatch({
          type: 'login',
          value: res.data.sessionId
        })
      }
        // dispatch({
        //   type: 'login'
        // })
    })
  }
}