import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import {
  Statistic,
  Icon,
  Row,
  Col
} from 'antd'
import 'antd/dist/antd.css'

const Profile=(props)=>(
  <div>
  <Row style={{margin:'0px 5px 30px 5px'}}>
    <Col span={12}>
    <Statistic title='User:' value={props.account} prefix={<Icon type="user" />} />
    </Col>
    <Col span={12}>
    <img alt="example" src="https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png" width='100' height='100'/>
    </Col>
  </Row>
  <Row style={{margin:'30px 5px'}}>
    <Col span={24}>
    <Statistic title='Address:' value={props.address} valueStyle={{overflow:'hidden',textOverflow: 'ellipsis',whiteSpace: 'nowrap',width:'90%'}} />
    </Col>
  </Row>
  <Row style={{margin:'30px 5px'}}>
    <Col span={12}>
    <Statistic title='Balance:' value={props.balance} precision={2}/>
    </Col>
    <Col span={12}>
    <Statistic title='Total Energy(kWh):' value={props.total} precision={2}/>
    </Col>
  </Row>
  <Row style={{margin:'30px 5px'}}>
    <Col span={12}>
    <Statistic title='Number of Deliveries:' value={props.deliveryNum} precision={0} />
    </Col>
    <Col span={12}>
    <Statistic title='Number of Purchases:' value={props.purchaseNum} precision={0} />
    </Col>
  </Row>
  </div>

)

export default Profile