import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link} from 'react-router-dom'
import {
  Button,
  Container,
  Grid,
  Header,
  List,
  Segment,
  Transition,
  Divider
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'
import HeaderBar from '../header/index'
import LiveData from './component/liveData'
import Feature from './component/systemFeature'
import Trending from './component/trending'
import eth from '../../statics/ethereum-blockchain.png'

class HomepageLayout extends Component{
  state={visibleSeg1: true,
         visibleSeg2: true,
         visibleSeg3: true,}
  
  handleScroll(){ 
    let pos=window.pageYOffset
    if(pos<100)
      this.setState({
        visibleSeg1:false,
        visibleSeg2:false,
        visibleSeg3:false
      })
    else if(pos<500)
      this.setState({
        visibleSeg1:true,
        visibleSeg2:false,
        visibleSeg3:false
      })
    else if(pos<1300)
      this.setState({
        visibleSeg1:true,
        visibleSeg2:true,
        visibleSeg3:false
      })
    else
      this.setState({
        visibleSeg1:true,
        visibleSeg2:true,
        visibleSeg3:true
      })
  }

  componentDidMount() {
    window.onscroll = () => this.handleScroll();
    this.props.initialHome();
  }

  render(){
    const {visibleSeg1,visibleSeg2,visibleSeg3} = this.state

    return(
  <HeaderBar>
    <Transition visible={visibleSeg1} animation='fade up' duration={700}>
      <Segment basic>
      <Feature />
      </Segment>
    </Transition>
    <Divider />
    <Transition visible={visibleSeg2} animation='fade up' duration={1000}>
    <Segment style={{ padding: '2em 0em 6em 0em' }} vertical>
      <Grid container stackable verticalAlign='middle'>
        <Grid.Row>
          <Grid.Column width={9} textAlign='center'>
            <div style={{fontSize: '2.5em',color:'black',fontFamily: '"Trebuchet MS", Helvetica, sans-serif'}}> The World Leading Asset Exchange Based on ERC721 </div>
            <img src={eth} alt='ethereum' />
          </Grid.Column>
          <Grid.Column floated='right' width={6}>
            <LiveData />
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    </Transition>
    <Segment style={{ padding: '0em' }} vertical>
      <Grid celled='internally' columns='equal' stackable>
        <Grid.Row textAlign='center'>
          <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em',backgroundColor: 'rgb(27,28,29)'}}>
          <Header as='h3' style={{ fontSize: '2em',color: 'white',fontFamily: '"Trebuchet MS", Helvetica, sans-serif'}}>
            The Most Powerful Decentralized Digital Asset Exchange.
          </Header>
            <p style={{ fontSize: '1.33em',color: 'white', fontFamily:'"Trebuchet MS", Helvetica, sans-serif'}}>
            More than electricity, more deals are coming soon.</p>
          </Grid.Column>
        </Grid.Row>
      <Transition visible={visibleSeg3} animation='fade up' duration={700}>
        <Grid.Row>
          <Grid.Column style={{marginTop:'3em'}} textAlign='center'>
            <Trending />
            <Link to='/exchange' onClick={this.props.handleSelect}>
            <Button secondary size='huge' content='Start Trading' style={{margin:'3em 0'}} />
            </Link>
          </Grid.Column>
        </Grid.Row>
      </Transition>
      </Grid>
    </Segment>
    <Segment inverted vertical style={{ padding: '5em 0em' }}>
      <Container>
        <Grid divided inverted stackable>
          <Grid.Row>
            <Grid.Column width={3}>
              <Header inverted as='h4' content='About' />
              <List link inverted>
                <List.Item as='a'>Sitemap</List.Item>
                <List.Item as='a'>Contact Us</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={3}>
              <Header inverted as='h4' content='Services' />
              <List link inverted>
                <List.Item as='a'>User Agreement</List.Item>
                <List.Item as='a'>Privacy Policy</List.Item>
                <List.Item as='a'>Legal Statement</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={7}>
              <Header as='h4' inverted>
                Extra
              </Header>
              <p>
                ©2019 PowExChain. ALL RIGHTS RESERVED
              </p>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </Segment>
  </HeaderBar>
    )}
}

const mapStateToProps = (state) => {
  return {
    loginStatus: state.logIn.login
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    handleSelect(){
      dispatch({
        type: 'changePage',
        value: 'Exchange'
      })
    },
    initialHome(){
      dispatch({
        type: 'changePage',
        value: 'Home'
      })
    }
  }
}

export default connect(null,mapDispatchToProps)(HomepageLayout);