import React, { Component } from 'react'
import { Link,Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import {
  Row,
  Col,
  Card
} from 'antd'
import 'antd/dist/antd.css'
import axios from 'axios'
import HeaderBar from '../header/index'
import Profile from './component/profile';
import Transaction from './component/transaction';

var data = null;

class Asset extends Component{

  constructor(props) {
    super(props);
    this.state={
      isLoaded: false
    }
  }

  componentDidMount(){
    //去掉！
    if(this.props.login){
    this.props.handleSelect();
    axios.get('http://211.71.8.213:8080/usr/account/'+this.props.sessionId).then((res)=>{
      data = JSON.parse(JSON.stringify(res.data));
      let sum=0;
      for(let i=0;i<data.purchase.length;i++){
        sum=sum+parseInt(data.purchase[i].amount);
      }
      data.total=sum;
      this.setState({isLoaded:true});
    });
    }
  }
  

  render(){
    //加上！
    if(!this.props.login){
      alert('Please log in !');
      return <Redirect to='/login' />;
    }
    else if(!this.state.isLoaded){
      return <p>Loading</p>
    }
    else{
    return(
      <HeaderBar>
        <div style={{background:'#D4D4D4'}}>
        <Row>
          <Col span={8}>
            <Card title='Profile' style={{margin:'5px',minHeight:'670px'}}>
            <Profile account={data.account} balance={data.balance} address={data.address} total={data.total} deliveryNum={data.deliveryNum} purchaseNum={data.purchaseNum} />
            </Card>
          </Col>
          <Col span={16}>
            <Row>
              <Col>
              <Card title='Purchases' style={{margin:'5px'}}>
              <Transaction data={data.purchase} side='buy' /> 
              </Card>
              </Col>
            </Row>
            <Row>
              <Col>
              <Card title='Deliveries' style={{margin:'5px'}}>
              <Transaction data={data.delivery} side='sell' /> 
              </Card>
              </Col>
            </Row>
          </Col>
        </Row>
        </div>
      </HeaderBar>
    )
    }
  }
} 

const mapStateToProps = (state) => {
  return {
    login: state.logIn.login,
    sessionId: state.logIn.sessionId
  }
}
 
const mapDispatchToProps = (dispatch) => {
  return {
    handleSelect(){
      dispatch({
        type: 'changePage',
        value: 'Asset'
      })
    }
  }
}


export default connect(mapStateToProps,mapDispatchToProps)(Asset);