import React, { Component } from 'react'
import { Link,Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import {Col,Row,Card} from 'antd'
import 'antd/dist/antd.css'
import {
  Grid,
  Segment,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'
import HeaderBar from '../header/index'
import Candlestick from './component/candleStick'
import Buy from './component/trade'
import Market from './component/market'
import Pool from './component/pool'
import axios from 'axios';

var time = new Date();
var current = time.toLocaleDateString('cn',{year: 'numeric', month: '2-digit', day: '2-digit' }).replace(/\//g,'');
var utc = time.getTime()+2592000000;
time.setTime(utc);
var expire = time.toLocaleDateString('cn',{year: 'numeric', month: '2-digit', day: '2-digit' }).replace(/\//g,'');

var pool = null;

class ViewExchange extends Component{

  constructor(props) {
    super(props);
    this.state={
      isLoaded: false
    }
  }

  refreshPool=()=>{
    axios.get('http://211.71.8.213:8080/tx/pool/' + this.props.sessionId).then((res) => { pool=JSON.parse(JSON.stringify(res.data.tx)); this.setState({ isLoaded: true }) })
  }

  componentDidMount(){
    if(this.props.login){
    this.props.handleSelect();
    this.refreshPool();
  }}

    render(){
      //加上！
      if(!this.props.login){
        alert('Please log in !');
        return <Redirect to='/login' />;
      }
      else if(!this.state.isLoaded){
        return <p>Loading</p>
      }
      else{
      return(
        <HeaderBar>
        <div style={{background:'#D4D4D4'}} >
          <Row>
            <Col xs={24} sm={24} md={16} lg={16}>
            <Card style={{margin:'5px'}}>
            <Candlestick />
            </Card>
            </Col>
            <Col xs={24} sm={24} md={8} lg={8}>
            <Card style={{margin:'5px'}} bodyStyle={{padding:'11.6px 24px'}}>
            <Market />
            </Card>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={16} lg={16}>
            <Card title='Txns Pool' bodyStyle={{padding:'16.1px 24px'}} headStyle={{fontSize:'1em',fontWeight:'bold',opacity:'0.65'}} style={{margin:'5px'}}>
              <Pool pool={pool} current={current} refreshPool={this.refreshPool.bind(this)} />
            </Card>
            </Col>
            <Col xs={24} sm={24} md={8} lg={8}>
              <Card style={{margin:'5px'}}>
              <Buy current={current} expire={expire} refreshPool={this.refreshPool.bind(this)} />
              </Card>
            </Col>
          </Row>
        </div>
      </HeaderBar>
      )
      }
    }
}



const mapStateToProps = (state) => {
  return {
    login: state.logIn.login,
    sessionId: state.logIn.sessionId
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    handleSelect(){
      dispatch({
        type: 'changePage',
        value: 'Exchange'
      })
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(ViewExchange);