import React, { Component,Fragment } from 'react'
import {connect} from 'react-redux'
import {
  Empty,
  List,
  Row,
  Col,
  Button,
  Divider
} from 'antd'
import 'antd/dist/antd.css'
import axios from 'axios';

class Order extends Component{
  constructor(){
    super();
  }

  render(){
    return(
      <Fragment>
      <Row style={{marginTop:'0.3em'}}>
        <Col span={4}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Type</div></Col>
        <Col span={4}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Amount/kWh</div></Col>
        <Col span={4}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Power/W</div></Col>
        <Col span={3}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Uint-Price</div></Col>
        <Col span={3}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Price</div></Col>
        <Col span={6}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Time</div></Col>
      </Row>
      <List
      dataSource={this.props.pool}
      style={{height:'157px'}}
      pagination={{pageSize:5,simple:'true',style:{marginTop:'-10px'}}}
      renderItem={(item,index) =>
      (<Fragment> <Divider style={{margin:'0'}} />
      <Row key={index} style={{marginTop:'0'}}>
        <Col span={4}><div style={{fontWeight:'500'}}>{item.type}</div></Col>
        <Col span={4}><div style={{fontWeight:'500'}}>{item.amount}</div></Col>
        <Col span={4}><div style={{fontWeight:'500'}}>{item.power}</div></Col>
        <Col span={3}><div style={{fontWeight:'500'}}>{(parseFloat(item.value)/parseFloat(item.amount)).toFixed(2)}</div></Col>
        <Col span={3}><div style={{fontWeight:'500'}}>{item.value}</div></Col>
        <Col span={3}><div style={{fontWeight:'500'}}>{item.timestampSell}</div></Col>
            <Col span={3}><Button onClick={() => {
              axios.put('http://211.71.8.213:8080/tx/purchase', {
                "sessionId": this.props.sessionId,
                "timestampBuy": this.props.current,
                "tx": [
                  {
                    "txHash": item.txHash
                  }
                ]
              }).then((res) => {
                if (res.data.result[0].msg === 'succeed') {
                  alert('Purchase successfully');
                  this.props.refreshPool();
                }
                else {
                  alert('Insufficient Balance')
                }
              })
            }}
          size='small' style={{ fontSize: '1em', color: '#FFFFFF', width: '50%', backgroundColor: '#FA5252', border: 'none' }}>Buy</Button></Col></Row></Fragment>)}
        />
      </Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    sessionId: state.logIn.sessionId
  }
}

export default connect(mapStateToProps,null)(Order);