import React from 'react'
import {
  Grid,
  Reveal,
  Container
  
} from 'semantic-ui-react'
import {Card} from 'antd'
import 'semantic-ui-css/semantic.css'
import 'antd/dist/antd.css'
import pic1 from './statics/security.png'
import pic2 from './statics/ethereum.png'
import pic3 from './statics/certificate.png'
import pic4 from './statics/fast.png'

const SystemFeature=()=>(
<Grid centered doubling columns='4' style={{padding:'2em 2em 0 2em'}}>
  <Grid.Column>
  <Reveal animated='small fade' style={{minHeight:'250px'}}>
    <Reveal.Content visible>
    <Card
    bordered={false}
    hoverable
    style={{ width: '85%' }}
    cover={<img alt="example" src={pic1} />}
  >
    <Card.Meta
      title={<h2 style={{fontFamily:'"Lucida Sans Unicode", "Lucida Grande", sans-serif'}} align='center'>Security</h2>}
    />
  </Card>    
    </Reveal.Content>
    <Reveal.Content hidden>
    <Container textAlign='left' style={{width:'60%'}}>
    <div style={{color:'black',fontFamily:'"Lucida Console", Monaco, monospace',fontSize:'1.2em',fontWeight:'500',display:'-webkit-box',  overflow: 'hidden', textOverflow: 'ellipsis', WebkitBoxOrient:'vertical',WebkitLineClamp:'9'}}>
    <br /><br />
    We are committed to protecting our digital assets and storing them in secure storage.
    </div>
    </Container>
    </Reveal.Content>
  </Reveal>
  </Grid.Column>

  <Grid.Column>
  <Reveal animated='small fade' style={{minHeight:'250px'}}>
    <Reveal.Content visible>
    <Card
    bordered={false}
    hoverable
    style={{ width: '85%' }}
    cover={<img alt="example" src={pic2} />}
  >
    <Card.Meta
      title={<h2 style={{fontFamily:'"Lucida Sans Unicode", "Lucida Grande", sans-serif'}} align='center'>Decentralized</h2>}
    />
  </Card>    
    </Reveal.Content>
    <Reveal.Content hidden>
    <Container textAlign='left' style={{width:'60%'}}>
    <div style={{color:'black',fontFamily:'"Lucida Console", Monaco, monospace',fontSize:'1.2em',fontWeight:'500',display:'-webkit-box',  overflow: 'hidden', textOverflow: 'ellipsis', WebkitBoxOrient:'vertical',WebkitLineClamp:'9'}}>
    <br /><br />
    The decentralized trading platform saves intermediary fees and is also more reliable.
    </div>
    </Container>
    </Reveal.Content>
  </Reveal>
  </Grid.Column>

  <Grid.Column>
  <Reveal animated='small fade' style={{minHeight:'250px'}}>
    <Reveal.Content visible>
    <Card
    bordered={false}
    hoverable
    style={{ width: '85%' }}
    cover={<img alt="example" src={pic3} />}
  >
    <Card.Meta
      title={<h2 style={{fontFamily:'"Lucida Sans Unicode", "Lucida Grande", sans-serif'}} align='center'>ERC721 Token</h2>}
    />
  </Card>    
    </Reveal.Content>
    <Reveal.Content hidden>
    <Container textAlign='left' style={{width:'60%'}}>
    <div style={{color:'black',fontFamily:'"Lucida Console", Monaco, monospace',fontSize:'1.2em',fontWeight:'500',display:'-webkit-box',  overflow: 'hidden', textOverflow: 'ellipsis', WebkitBoxOrient:'vertical',WebkitLineClamp:'9'}}>
    <br/><br/>
    Each digital asset is a Non-Fungible Token generated by ERC721.
    </div>
    </Container>
    </Reveal.Content>
  </Reveal>
  </Grid.Column>

  <Grid.Column>
  <Reveal animated='small fade' style={{minHeight:'250px'}}>
    <Reveal.Content visible>
    <Card
    bordered={false}
    hoverable
    style={{ width: '85%' }}
    cover={<img alt="example" src={pic4} />}
  >
    <Card.Meta
      title={<h2 style={{fontFamily:'"Lucida Sans Unicode", "Lucida Grande", sans-serif'}} align='center'>Rapid Processing</h2>}
    />
  </Card>    
    </Reveal.Content>
    <Reveal.Content hidden>
    <Container textAlign='left' style={{width:'60%'}}>
    <div style={{color:'black',fontFamily:'"Lucida Console", Monaco, monospace',fontSize:'1.2em',fontWeight:'500',display:'-webkit-box',  overflow: 'hidden', textOverflow: 'ellipsis', WebkitBoxOrient:'vertical',WebkitLineClamp:'9'}}>
    <br /><br />
    Fast block confirmation ensures transactions are completed within several seconds.
    </div>
    </Container>
    </Reveal.Content>
  </Reveal>
  </Grid.Column>

</Grid>
)

export default SystemFeature;