import React,{Component} from 'react'
import ReactEcharts from "echarts-for-react"
import {
  Grid,
  Segment,
  Icon
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'

function randomData() {
  now = new Date(+now + oneDay);
  value = value + Math.random() * 10-5;
  return {
      name: now.toString(),
      value: [
          [now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'),
          Math.round(value)
      ]
  }
}

var data = [];
var now = +new Date(2019, 1, 1);
var oneDay = 24 * 3600 * 1000;
var value = Math.random() * 1000;
for (var i = 0; i < 500; i++) {
  data.push(randomData());
}


class Trend extends Component{
  componentDidMount() {
    let instance = this.echarts_react.getEchartsInstance();
    this.interval = setInterval(function () {
      for (var i = 0; i < 5; i++) {
          data.shift();
          data.push(randomData());
      }
      instance.setOption({
          series: [{
              data: data
          }]
      });
  }, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render(){
    return(
      <ReactEcharts
      ref={(e)=>{this.echarts_react=e}}
      option={this.getOption()}
      style={{height: '60px'}}
      opts={{renderer: 'svg'}}
       />
    )
  }

  getOption(){
    return(
      {
        xAxis: {
            type: 'time',
            show: false,
            splitLine: {
                show: false
            }
        },
        yAxis: {
            type: 'value',
            show: false,
            boundaryGap: [0, '100%'],
            splitLine: {
                show: false
            }
        },
        series: [{
            name: '模拟数据',
            type: 'line',
            showSymbol: false,
            hoverAnimation: false,
            data: data,
            smooth: true
        }],
        color:['#C0C0C0'],
    }
    )
  }
}

const Trending=()=>(
  <Segment raised style={{width:'80%',margin:'0 auto'}}>
  <Grid verticalAlign='middle' divided='vertically'>
  <Grid.Row>
    <Grid.Column width={2} style={{margin:'5px 0'}}>
      <div style={{fontSize:'1.2em',color:'#969696'}}>#</div>
    </Grid.Column>
    <Grid.Column width={6} style={{margin:'5px 0'}}>
      <div style={{fontSize:'1.2em',color:'#969696'}}>TYPE</div>
    </Grid.Column>
    <Grid.Column width={2} style={{margin:'5px 0'}}>
      <div style={{fontSize:'1.2em',color:'#969696'}}>PRICE</div>
    </Grid.Column>
    <Grid.Column width={2} style={{margin:'5px 0'}}>
      <div style={{fontSize:'1.2em',color:'#969696'}}>24H CHANGE</div>
    </Grid.Column>
    <Grid.Column width={4} style={{margin:'5px 0'}}>
      <div style={{fontSize:'1.2em',color:'#969696'}}>&emsp;CHART</div>
    </Grid.Column>
  </Grid.Row>

  <Grid.Row>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>1</div>
    </Grid.Column>
    <Grid.Column width={6}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>
      <Icon size='large' color='orange' name='sun'/>Solar Power</div>
    </Grid.Column>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>￥0.523</div>
    </Grid.Column>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>0.67%</div>
    </Grid.Column>
    <Grid.Column width={4} style={{margin:'0'}}>
      <Trend />
    </Grid.Column>
  </Grid.Row>

  <Grid.Row>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>2</div>
    </Grid.Column>
    <Grid.Column width={6}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>
      <Icon size='large' color='grey' name='send'/>Wind Power</div>
    </Grid.Column>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>￥0.505</div>
    </Grid.Column>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>0.32%</div>
    </Grid.Column>
    <Grid.Column width={4} style={{margin:'0'}}>
      <Trend />
    </Grid.Column>
  </Grid.Row>

  <Grid.Row>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>3</div>
    </Grid.Column>
    <Grid.Column width={6}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>
      <Icon size='large' color='blue' name='tint'/>Hydropower</div>
    </Grid.Column>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>￥0.528</div>
    </Grid.Column>
    <Grid.Column width={2}>
      <div style={{fontSize:'1.5em',fontWeight:600}}>-0.13%</div>
    </Grid.Column>
    <Grid.Column width={4} style={{margin:'0'}}>
      <Trend />
    </Grid.Column>
  </Grid.Row>

  </Grid>
  </Segment>
)

export default Trending