import React, { Component,Fragment } from 'react'
import {
  List,Col,Row,Button
} from 'antd'
import 'antd/dist/antd.css'


const Transaction=(props)=>(
  <Fragment>
  <Row>
    <Col span={7}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Time</div></Col>
    <Col span={3}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Type</div></Col>
    <Col span={3}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Status</div></Col>
    <Col span={4}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Uint-price</div></Col>
    <Col span={4}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Amount</div></Col>
    <Col span={3}><div style={{fontSize:'1em',color:'#7F8FA4'}}>Price</div></Col>
  </Row>
  <List 
  style={{height:'200px'}}
  dataSource={props.data}
  pagination={{
    pageSize: 6,
  }}
  renderItem={(item,index)=>
  <Row key={index} style={{marginTop:'0.2em'}}>
    <Col span={7}><div style={{fontSize:'1.1em',fontWeight:'500'}}>{props.side==='buy'?item.timestampBuy:item.timestampSell}</div></Col>
    <Col span={3}><div style={{fontSize:'1.1em',fontWeight:'500'}}>{item.type}</div></Col>
    <Col span={3}><div style={{fontSize:'1.1em',fontWeight:'500'}}>{item.status}</div></Col>
    <Col span={4}><div style={{fontSize:'1.1em',fontWeight:'500'}}>{(parseFloat(item.value)/parseFloat(item.amount)).toFixed(2)}</div></Col>
    <Col span={4}><div style={{fontSize:'1.1em',fontWeight:'500'}}>{item.amount}</div></Col>
    <Col span={3}><div style={{fontSize:'1.1em',fontWeight:'500'}}>{item.value}</div></Col>
  </Row>}
  />
  </Fragment>
)

export default Transaction