import React,{Component} from 'react'
import 'semantic-ui-css/semantic.css'
import { Button, Form, Grid, Header, Icon, Message, Segment } from 'semantic-ui-react'
import { Link,Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import {login} from './store/actionCreators'

class LoginForm extends (Component) {
  componentDidMount=()=>{
    if(!this.props.login)
    this.props.refresh();
  }
  render(){
    if(this.props.login){
      return <Redirect to='/' />
    }
    else{
    return(
      <Grid textAlign='center' style={{ height: '100vh' }} verticalAlign='middle'>
        <Grid.Column style={{ maxWidth: 450 }}>
          <Header as='h2' color='black' textAlign='center'>
            <Icon name='sign in alternate'/> Log in to your account
          </Header>
          <Form size='large'>
            <Segment stacked>
              <Form.Input value={this.props.account} onChange={this.props.handleChange} fluid icon='user' iconPosition='left' placeholder='Username' />
              <Form.Input
                fluid
                icon='lock'
                iconPosition='left'
                placeholder='Password'
                type='password'
                value={this.props.password}
                onChange={this.props.handleChange}
              />
    
              <Button color='black' fluid size='large' onClick={()=>this.props.submit(this.props.account,this.props.password)}>
                Login
              </Button>
            </Segment>
          </Form>
          <Message>
            New to us? <a href='#'>Sign Up</a>
          </Message>
        </Grid.Column>
      </Grid>
    )
    }
  }
} 

const mapStateToProps=(state)=>{
    return{
      login: state.logIn.login,
      account: state.logIn.account,
      password: state.logIn.password
    }
}

const mapDispatchToProps = (dispatch) => {
  return {
    submit(account,password){
      dispatch(login(account,password))
    },

    handleChange(e){
      if(e.target.placeholder==='Username')
      dispatch({
        type: 'account',
        value: e.target.value
      })
      else
      dispatch({
        type: 'password',
        value: e.target.value
      })
    },

    refresh(){
      dispatch({
        type: 'refresh'
      })
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(LoginForm)